import React from 'react'
import { Link } from 'react-router-dom'

const Footer = () => {
	return (
		<div className='footer'>
			<div className='footer__left'>
				<Link to='/' className='header__logo footer__logo'>
					<h1>Crown Store</h1>
				</Link>
				<p> 2024.   -</p>
			</div>
			<div className='footer__info'>
				<h3 className='footer__info-title'>  </h3>
				<p className='footer__info-text'>
					: ., 1 . , .11 ( )
				</p>
				<p className='footer__info-text'>: 8 (960) 716-69-22</p>
				<div className='footer__info-link'>
					<p className='footer__info-text'>  VK:</p>
					<a href='https://vk.com/skrona' title='  '>
						<img className='footer__info-img' src={require('../../assets/vk-icon.png')} alt='vk' />
					</a>
				</div>
			</div>
		</div>
	)
}

export default Footer
